export default {
	data() {
		return {
			introduceList:[],
			project:[],
		}
	},
	onLoad:function(){ 
		var _self = this
		_self.getInfo()
	},
	methods: {
		getInfo:function(){
			var _self = this
			uni.request({
				url:_self.$api+"dockingManager/releaseMainQuery",
				data:{id:"0",pull:0,optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					_self.introduceList = res.data
				}
			})
		},       
		
		}
	
}
